img {
    width: 50px;
}
div {
    min-height: 50px;
}
.add-button {
    margin-top: 20px;
}
.header div {
    padding: 10px 15px;
}
.modal-footer {
    text-align: center;
}
.help-block {
    display: inline-block;
    margin: 0;
}

.header {
    background-color: #337ab7;
    color: white;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
}

.panel {
    line-height: 50px;
    &:hover {
        cursor: pointer;
        background-color: #d9edf7;
    }
}
.table-striped>tbody>tr:nth-of-type(odd) {
    background-color: #d9edf7;
}